import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

export const instituteCountStatsFunction = (builder) => {
  return builder.query({
    query: (data) => `/v2/ins/${data?.id}/stats`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => response?.stats,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const instituteAddAcademicDepartmentFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/academic/department/ins/${data?.id}/department/query`,
      method: POST,
      body: data.bodyParameter,
    }),
  });
};

export const instituteAllAcademicDepartmentFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v2/academic/department/${data?.id}/department?page=${data?.page}&limit=${data?.limit}&search=${data.search}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.all_depart,
        };
      else
        return {
          page: arg?.page,
          list: response?.all_depart,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

