import {
  useInstituteAddAcademicDepartmentMutation,
  useInstituteAllAcademicDepartmentQuery,
  useAcademicDepartmentAllSubjectMasterQuery,
  useAcademicDepartmentMapSubjectMasterMutation,
  useAcademicDepartmentAllBatchQuery,
  useAcademicDepartmentAllClassMasterQuery,
  useClassMasterAllStudentQuery,
  useAcademicDepartmentAllStudentQuery,
  useAcademicDepartmentAllStaffQuery,
  useInstituteDepartmentDashboardQuery,
  useInstituteCountStatsQuery,
} from "../../../Services/AcademicDepartmentManager/academicDepartmentManagerApiDynamic";

export const useInstituteCountStats = (args) => {
  const { data, refetch, isFetching } = useInstituteCountStatsQuery(args.data, {
    skip: args.skip,
  });
  return {
    instituteCountStats: data,
    instituteCountStatsRefetch: refetch,
    instituteCountStatsLoading: isFetching,
  };
};
export const useInstituteAddAcademicDepartment = () => {
  const [instituteAddAcademicDepartment] =
    useInstituteAddAcademicDepartmentMutation();
  return [instituteAddAcademicDepartment];
};
